.projects-navbar-container {
  width: 100%;
  height: 60px;
  display: flex;
  z-index: 1000;
  position: relative;
  box-shadow: 5px 2px 10px 0px #d4d4d4;
  align-items: center;
  padding-top: 0px;
  padding-left: 0px;
  padding-right: 0px;
  flex-direction: row;
  padding-bottom: 0px;
  justify-content: center;
  background-color: #ffffff;
}
 
.projects-navbar-navbar-interactive {
  width: 100%;
  display: flex;
  max-width: 100%;
  align-items: center;
  padding-top: var(--dl-layout-space-twounits);
  padding-left: var(--dl-layout-space-threeunits);
  padding-right: var(--dl-layout-space-threeunits);
  padding-bottom: var(--dl-layout-space-twounits);
  justify-content: center;
}
 
.projects-navbar-navlink {
  display: contents;
}
 
.projects-navbar-component1 {
  text-decoration: none;
}
 
.projects-navbar-text10 {
