.navbar-container {
  width: 100%;
  height: 95px;
  display: flex;
  position: relative;
  align-items: center;
  padding-top: 0px;
  padding-left: 0px;
  padding-right: 0px;
  flex-direction: row;
  padding-bottom: 0px;
  justify-content: center;
  background-color: #ffffff;
}
 
.navbar-navbar-interactive {
  width: 100%;
  display: flex;
  max-width: var(--dl-layout-size-maxwidth);
  align-items: center;
  padding-top: var(--dl-layout-space-twounits);
  padding-left: var(--dl-layout-space-threeunits);
  padding-right: var(--dl-layout-space-threeunits);
  padding-bottom: var(--dl-layout-space-twounits);
  justify-content: center;
}
 
.navbar-text10 {
  display: inline-block;
}
 
.navbar-desktop-menu {
  flex: 1;
  display: flex;
  justify-content: space-between;
}
 
.navbar-links1 {
  flex: 1;
  display: flex;
  align-self: center;
  align-items: center;
  padding-left: var(--dl-layout-space-twounits);
  padding-right: var(--dl-layout-space-twounits);
  flex-direction: row;
  justify-content: flex-end;
}
 
.navbar-navlink {
  transition: 0.3s;
  text-decoration: none;
}
 
.navbar-navlink:hover {
  fill: #3b5892;
  color: #3b5892;
}
 
.navbar-text11 {
  transition: 0.3s;
  margin-left: var(--dl-layout-space-threeunits);
}
 
.navbar-text11:hover {
  fill: #3b5892;
  color: #3b5892;
}
 
.navbar-text12 {
  transition: 0.3s;
  margin-left: var(--dl-layout-space-threeunits);
}
 
.navbar-text12:hover {
  fill: #3b5892;
  color: #3b5892;
}
 
.navbar-buttons1 {
  display: flex;
  align-items: center;
  border-color: var(--dl-color-theme-neutral-dark);
  border-width: 0px;
  padding-left: var(--dl-layout-space-unit);
  flex-direction: row;
  justify-content: space-between;
  border-left-width: 1px;
}
 
.navbar-login1 {
  width: var(--dl-layout-size-medium);
  transition: 0.3s;
  border-width: 0px;
  margin-right: var(--dl-layout-space-unit);
  text-decoration: none;
  background-color: transparent;
}
 
.navbar-login1:hover {
  color: #ffffff;
  background-color: #3b5892;
}
 
.navbar-register1 {
  fill: var(--dl-color-theme-neutral-dark);
  color: var(--dl-color-theme-neutral-dark);
  width: var(--dl-layout-size-medium);
  transition: 0.3s;
  border-width: 0px;
  text-decoration: none;
  background-color: transparent;
}
 
.navbar-register1:hover {
  fill: var(--dl-color-theme-neutral-dark);
  color: #fdfdfd;
  background-color: #3b5892;
}
 
.navbar-burger-menu {
  display: none;
}
 
.navbar-icon10 {
  width: var(--dl-layout-size-xsmall);
  height: var(--dl-layout-size-xsmall);
}
 
.navbar-mobile-menu {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  display: none;
  padding: 32px;
  z-index: 100;
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
 
.navbar-nav {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
 
.navbar-top {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: var(--dl-layout-space-threeunits);
  justify-content: space-between;
}
 
.navbar-text15 {
