  }
}
 
@media(max-width: 479px) {
  .navbar-navbar-interactive {
    padding: var(--dl-layout-space-unit);
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .navbar-mobile-menu {
    left: 0px;
    right: 0px;
    margin: auto;
    padding: 16px;
  }
  .navbar-links2 {
    gap: var(--dl-layout-space-twounits);
    align-self: center;
